<template>
  <div>
    <router-view v-if="isAlive"></router-view>
  </div>
</template>

<script>
  export default {
    name:'App',
    provide(){
      reload:this.reload
    },
    data(){
      return{
        isAlive:true
      }
    },
    methods:{
      reload(){
        this.isAlive = false
        this.$nextTick(function () {
            this.isAlive = true
        })
      }
    }
  }
</script>

